/* App.css */

.container {
  width: 80%;
  margin: 50px auto;
  text-align: center;
}

button.add-button, button.reiniciar-button {
  padding: 10px 15px;
  font-size: 16px;
  background-color: #cb79e4;
  color: white;
  border: none;
  cursor: pointer;
}

button.add-button:hover, button.reiniciar-button:hover {
  background-color: #cb79e4;
}

button.reiniciar-button {
  background-color: #cb79e4; /* Cor vermelha para indicar ação */
}

button.reiniciar-button:hover {
  background-color: #cb79e4;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

th, td {
  padding: 12px;
  border: 1px solid #ddd;
  text-align: center;
}

th {
  background-color: #cb79e4;
  color: white;
}

tr:nth-child(even) {
  background-color: #f2f2f2;
}

/* Adicionar o estilo para o formulário de adicionar colaborador */
.add-colaborador-form {
  background-color: #f9f9f9;
  padding: 20px;
  border: 1px solid #ddd;
  margin-top: 20px;
  border-radius: 5px;
}

.add-colaborador-form input {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.add-colaborador-form button {
  background-color: #4CAF50;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

.add-colaborador-form button:hover {
  background-color: #45a049;
}
.add-colaborador-container {
  background-color: white;
  padding: 20px;
  border: 1px solid #ddd;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 300px;
  margin: auto;
}

.add-colaborador-container input {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.add-colaborador-container button {
  padding: 8px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.add-colaborador-container button:hover {
  background-color: #45a049;
}
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  text-align: center;
}

button {
  margin-top: 20px;
}
